import React from 'react';
import './App.css';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import logo from './belgie.png'

class Corona extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      belgie: [],
      italie: []
    }
  }

  componentDidMount() {
   fetch("https://thevirustracker.com/free-api?countryTimeline=BE")
     .then(res => res.json())
     .then(
       (result) => {
         var obj = result.timelineitems
         this.setState({
           isLoaded: true,
           items: obj[0],
         })
       },
       // Note: it's important to handle errors here
       // instead of a catch() block so that we don't swallow
       // exceptions from actual bugs in components.
       (error) => {
         this.setState({
           isLoaded: true,
           error
         });
       }
     )

     fetch("https://thevirustracker.com/free-api?countryTimeline=IT")
       .then(res => res.json())
       .then(
         (result) => {
           var obj = result.timelineitems
           this.setState({
             isLoaded: true,
             italie: obj[0],
           })
         },
         // Note: it's important to handle errors here
         // instead of a catch() block so that we don't swallow
         // exceptions from actual bugs in components.
         (error) => {
           this.setState({
             isLoaded: true,
             error
           });
         }
       )

     fetch("https://thevirustracker.com/free-api?countryTotal=BE")
       .then(res => res.json())
       .then(
         (result) => {
           this.setState({
             isLoaded: true,
             belgie: result.countrydata,
           })
         },
         // Note: it's important to handle errors here
         // instead of a catch() block so that we don't swallow
         // exceptions from actual bugs in components.
         (error) => {
           this.setState({
             isLoaded: true,
             error
           });
         }
       )
 }

  render() {
    const { error, isLoaded, items, belgie, italie } = this.state;
    var totaalBesmet = belgie.map(a => a.total_cases)
    var totaalHersteld = belgie.map(a => a.total_recovered)
    var totaalDoden = belgie.map(a => a.total_deaths)
    var vandaagBesmet = belgie.map(a => a.total_new_cases_today)
    var vandaagDood = belgie.map(a => a.total_new_deaths_today)
    var arrayHistorie = Object.entries(items)
    var arrayHistorieItalie = Object.entries(italie)
    var historie = arrayHistorie.map(a => a[1].total_cases)
    var historieItalie = arrayHistorieItalie.map(a => a[1].total_cases)
    historie.pop()
    historie.pop()
    historieItalie.pop()
    historieItalie.pop()
    console.log(historie)

    const options = {
      title: {
        text: 'Besmettingen in Belgie sinds 23/01/20'
      },
      series: [{
        name: 'België',
        data: historie
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 500,
        height: 400
      }
    }

    const optionsItalie = {
      title: {
        text: 'Besmettingen in Italie sinds 23/01/2020'
      },
      series: [{
        name: 'Italië',
        data: historieItalie
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 500,
        height: 400
      }
    }
    const optionsTwee = {
      title: {
        text: 'Besmettingen in Belgie sinds 23/01/20'
      },
      series: [{
        name: 'België',
        data: historie
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 300,
        height: 400
      }
    }

    const optionsItalieTwee = {
      title: {
        text: 'Besmettingen in Italie sinds 23/01/2020'
      },
      series: [{
        name: 'Italië',
        data: historieItalie
      }],
      xAxis: {
        visible: false
      },
      chart: {
        type: 'line',
        width: 300,
        height: 400
      }
    }

    if (error) {
      return <div><header className="App-header">Error: {error.message}</header></div>;
    } else if (!isLoaded) {
      return <div><header className="App-header">Loading...</header></div>;
    } else {
  return (
    <div className="App">
      <header className="App-header">
        <h1 id="corona">Corona in België</h1>
        <img src={logo} alt="BE" id="belgie"/>
        <div>
          <div className="flex">
              <div className="block">
                <h3>Totaal besmet</h3>
                <h4 className="red">{totaalBesmet}</h4>
              </div>
              <div className="block">
                <h3>Totaal Hersteld</h3>
                <h4 className="green">{totaalHersteld}</h4>
              </div>
              <div className="block">
                <h3>Totaal gestorven</h3>
                <h4 className="red">{totaalDoden}</h4>
              </div>
              <div className="block">
                <h3>Vandaag besmet</h3>
                <h4 className="red">+ {vandaagBesmet}</h4>
              </div>
              <div className="block">
                <h3>Vandaag gestorven</h3>
                <h4 className="red">+ {vandaagDood}</h4>
              </div>
          </div>
        </div>
        <h2 className="titel-vergelijking">Vergelijking België - Italië</h2>
        <div className="chartDisplay">
          <div className="chartGroot"><HighchartsReact highcharts={Highcharts} options={options}/></div>
          <div className="chartKlein"><HighchartsReact highcharts={Highcharts} options={optionsTwee}/></div>
          <div className="chartGroot"><HighchartsReact highcharts={Highcharts} options={optionsItalie}/></div>
          <div className="chartKlein"><HighchartsReact highcharts={Highcharts} options={optionsItalieTwee}/></div>
        </div>
      </header>
    </div>
  )};
}}

export default Corona;
